import { MenuFoldOutlined, SearchOutlined, QuestionCircleTwoTone, FilterOutlined } from "@ant-design/icons";
import { Button, Col, Drawer, Form, Row, Tooltip, PageHeader, Select, Table, Popover, Tabs } from "antd";
import SearchFilterDropdown from "components/shared-components/SearchFilter";
import TableComponent from "components/shared-components/TableComponent/Index";
import TableMenuItem from "components/shared-components/TableMenuItem";
import {
  DATE_FORMAT,
  LIGHT_NAVY_BLUE_COLOR,
  SORT_DIRECTIONS,
  FILTER_FORM_FIELDS,
  SELECT_TEXT,
  REPORTS_DESCRIPTION,
} from "constants/CommonConstants";
import { MENU_ITEMS, MENU_ITEMS_TYPES } from "constants/EmployeeLoginDetails";
import useIntlMessage from "hooks/useIntlMessage";
import moment from "moment";
import { useEffect, useMemo, useRef, useState } from "react";
import Utils from "utils";
import FileHandler from "utils/fileHandler";
import billableAgingReportService from "services/BillableAgingReportService";
import {
  BILLING_AGING_COLUMNS,
  FILE_NAME,
  NESTED_DATA_TABLE_COLUMNS,
  CURRENTLY_ACTIVE,
  ALL_DATA_TABLE_COLUMNS,
} from "constants/BillableAgingConstants";
import { useDispatch, useSelector } from "react-redux";
import LocationPicker from "components/shared-components/LocationPicker";
import { CHILDREN, SEARCH } from "../../../../constants/CommonConstants";
import { cloneDeep } from "lodash";
import { showLoader } from "redux/actions/Loader";
import BillableAgingReportSummary from "./BillableAgingReportSummary";
import BillableAgingReportVisual from "./BillableAgingReportVisual";
import { setBillableAgingReportColumns } from "../../../../redux/actions/MyTeams";

const ALL = "All";

const { Option } = Select;
const defaultSearchParams = {
  city: [ALL],
  account: ALL,
};

const BillableAgingReport = () => {
  const dispatch = useDispatch();
  const getMessage = useIntlMessage();
  const [open, setOpen] = useState(false);
  const [filteredInfo, setFilteredInfo] = useState({});
  const [sortedInfo, setSortedInfo] = useState({});
  const [BillableAgingReportData, setBillableAgingReportData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [searchParams, setSearchParams] = useState(defaultSearchParams);
  const allLocations = useSelector(state => state.myTeams.allLocations);
  const accountsList = useSelector(store => store.myTeams.accountsList);
  const [visibility, setVisibility] = useState();
  const [isMenuVisible, setIsMenuVisible] = useState(false);
  const [tableKey, setTableKey] = useState(0);
  const [nestedReportData, setNestedReportData] = useState([]);
  const [openPopOver, setOpenPopOver] = useState(false);
  const [isExported, setIsExported] = useState(false);
  const [exportVisibleData, setExportVisibleData] = useState([]);
  const [nestedDataColumns, setNestedDataColumns] = useState([]);
  const [quickActionBtns, setQuickActionBtns] = useState([]);
  const [dynamicColumns, setDynamicColumns] = useState([]);
  const [initialVisibleColumns, setInitialVisibleColumns] = useState([]);
  const [nestedVisibleColumns, setNestedVisibleColumns] = useState();
  const [filteredVisibleData, setFilteredVisibleData] = useState([]);
  const visibleFilteredColumns = useSelector(state => state.reports?.billableAgingReportColumns);
  let searchInput = useRef(null);
  let currentVisibleTableData = [];
  const [form] = Form.useForm();
  useEffect(() => {
    return () => {
      dispatch(showLoader(false));
    };
  }, []);
  const getColumnSearchProps = (field, isNested = false, item) => {
    let filterObj = {
      filterIcon: filtered => <SearchOutlined color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined} />,
    };
    if (field.type === "select") {
      if (!Utils.isEmpty(field.options)) {
        filterObj.filters = field.options.map(option => ({
          text: getMessage(option.label),
          value: option.value,
        }));
      }
      filterObj.filterIcon = filtered => (
        <FilterOutlined
          id={`allocation-change-search-btn-${field.key}`}
          color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined}
        />
      );
      filterObj.onFilter = (value, record) => record[field.key] === value;
    } else {
      filterObj.filterDropdown = ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
        <SearchFilterDropdown
          setSelectedKeys={setSelectedKeys}
          selectedKeys={selectedKeys}
          confirm={confirm}
          field={field}
          clearFilters={clearFilters}
          handleSearch={handleSearch}
          dataIndex={field.key}
          searchInput={searchInput}
          handleReset={handleReset}
          label={field.title}
          dataSource={isNested ? item?.allocations : BillableAgingReportData}
        />
      );
      filterObj.onFilter = (value, record) => {
        if (field.type === FILTER_FORM_FIELDS.MONTH.value) {
          const _value = value.format(DATE_FORMAT).toString();
          const timeStampToDate = moment(record[field.key])?.format(DATE_FORMAT)?.toString();
          return timeStampToDate.toLowerCase().includes(_value.toLowerCase());
        }
        return record[field.key]?.toString().toLowerCase().includes(value.toLowerCase());
      };
      filterObj.onFilterDropdownVisibleChange = visible => {
        if (visible) {
          setTimeout(() => searchInput && searchInput.select && searchInput.select());
        }
      };
    }
    return filterObj;
  };

  const generateDynamicColumns = columns => {
    return columns.map(field => {
      return {
        title: (
          <Tooltip title={field.title} placement="top">
            <span>{field.title}</span>
          </Tooltip>
        ),
        dataIndex: field.key,
        key: field.key,
        sorter: field.sortable && ((a, b) => Utils.antdTableSorter(a, b, field.key)),
        sortDirections: SORT_DIRECTIONS,
        isVisible: visibility[field.key],
        ...(field.searchable ? getColumnSearchProps(field) : null),
        render: (_, record) => {
          return (
            <Tooltip title={record[field.key]} placement="top">
              <span id={field.key}>{record[field.key]}</span>
            </Tooltip>
          );
        },
      };
    });
  };

  const nestedColumns = item =>
    nestedDataColumns?.map(field => {
      return {
        title: (
          <Tooltip title={field.title} placement="top">
            <span>{field.title}</span>
          </Tooltip>
        ),
        dataIndex: field.key,
        key: field.key,
        sorter:
          field.key === NESTED_DATA_TABLE_COLUMNS.ALLOCATION_AGING.key
            ? null
            : (a, b) => Utils.antdTableSorter(a, b, field.key),
        ...(field.key === NESTED_DATA_TABLE_COLUMNS.ALLOCATION_AGING.key
          ? null
          : getColumnSearchProps(field, true, item)),
        render: (_, record) => {
          if (
            field.key === NESTED_DATA_TABLE_COLUMNS.ALLOCATION_END_DATE.key &&
            record[field.key] === CURRENTLY_ACTIVE
          ) {
            return (
              <div>
                <span style={{ padding: "5px" }} className="highlight-class">
                  {record[field.key]}
                </span>
              </div>
            );
          }
          return record[field.key];
        },
      };
    });
  const nestedTableRendered = item => {
    return (
      <Table
        columns={nestedColumns(item)}
        summary={data => isExported && filterData(data, item.employeeId, fetchVisibleTableData)}
        dataSource={item?.allocations}
        pagination={false}
        showSorterTooltip={false}
      />
    );
  };
  const filteredColumns = useMemo(
    () => generateDynamicColumns(dynamicColumns).filter(item => item.isVisible),
    [visibility, loading],
  );

  const handleChange = (pagination, filters, sorter, extra) => {
    setFilteredVisibleData(extra.currentDataSource);
    setFilteredInfo(filters);
    setSortedInfo(sorter);
  };

  const handleSearch = (selectedKeys, search, dataIndex) => {
    search();
  };

  const handleReset = (clearFilters, confirm) => {
    clearFilters();
    confirm();
  };
  const handleMenuVisibleChange = flag => {
    setIsMenuVisible(flag);
  };

  const onColHide = data => {
    const _visibility = { ...visibility, [data.key]: !visibility[data.key] };
    dispatch(setBillableAgingReportColumns(_visibility));
    setVisibility({ ..._visibility });
  };

  const clearFilters = () => {
    form.resetFields();
    // setTableKey(tableKey => tableKey + 1);
    // setFilteredInfo({});
    // setSortedInfo({});
    setVisibility(initialVisibleColumns);
  };

  const onMenuItemClick = ({ key }) => {
    switch (key) {
      case MENU_ITEMS_TYPES.clearAllFilters:
        clearFilters();
        break;
      case MENU_ITEMS_TYPES.exportAllExcel:
      case MENU_ITEMS_TYPES.exportVisibleExcel:
        exportToExcel(key);
        break;
      case MENU_ITEMS_TYPES.exportAllPDF:
      case MENU_ITEMS_TYPES.exportVisiblePDF:
        exportToPDF(key);
        break;
      case MENU_ITEMS_TYPES.exportAllVisibleExcel:
        exportToExcel(key);
        break;
      case MENU_ITEMS_TYPES.exportAllVisiblePDF:
        exportToPDF(key);
        break;
      default:
        return;
    }
  };
  const exportToExcel = type => {
    Utils.exportToExcelNested({
      type: type,
      dynamicColumns: dynamicColumns,
      reportData: BillableAgingReportData,
      visibleReportData: currentVisibleTableData,
      visibility: visibility,
      fileName: FILE_NAME,
      nestedVisibleColumns: nestedVisibleColumns,
      exportVisibleData: exportVisibleData,
      nestedDataColumns: nestedDataColumns,
      currentDataSource: filteredVisibleData.length > 0 ? filteredVisibleData : BillableAgingReportData,
    });
    setIsMenuVisible(false);
  };

  const exportToPDF = type => {
    const visibleColumn = [...filteredColumns, ...nestedColumns(null)];
    const allData = [...generateDynamicColumns(dynamicColumns), ...nestedColumns(null)];
    Utils.exportToPDFNested({
      type: type,
      dynamicColumns: dynamicColumns,
      reportData: BillableAgingReportData,
      visibleReportData: currentVisibleTableData,
      visibleReportColumns: filteredColumns,
      tableDynamicColumns: generateDynamicColumns(dynamicColumns),
      visibility: visibility,
      fileName: FILE_NAME,
      nestedVisibleColumns: nestedVisibleColumns,
      exportVisibleData: exportVisibleData,
      nestedDataColumns: nestedDataColumns,
      visibleColumn: visibleColumn,
      allData: allData,
      currentDataSource: filteredVisibleData.length > 0 ? filteredVisibleData : BillableAgingReportData,
    });
    setIsMenuVisible(false);
  };
  const showDrawer = () => {
    setOpen(true);
    setIsExported(true);
  };

  const onClose = () => {
    setOpen(false);
  };
  const fetchBillingAgingReport = async params => {
    try {
      setLoading(true);
      dispatch(showLoader(true));
      const records = await billableAgingReportService.getReport(params);
      if (records) {
        const { data, nestedData } = billableAgingReportService.prepareData(records.data);
        const { columns, quickActions, nestedColumns } = records;
        if (!!columns.length) {
          let initailColumns = Utils.createObjFromArray(columns);
          let initialNestedColumns = Utils.createObjFromArray(nestedColumns);
          if (!Utils.isEmpty(visibleFilteredColumns)) {
            setVisibility(visibleFilteredColumns);
            setInitialVisibleColumns(visibleFilteredColumns);
            setDynamicColumns(columns);
            setNestedDataColumns(nestedColumns);
          } else {
            setNestedVisibleColumns(initialNestedColumns);
            setVisibility(initailColumns);
            setInitialVisibleColumns(initailColumns);
            setDynamicColumns(columns);
            setNestedDataColumns(nestedColumns);
          }
        }
        setBillableAgingReportData(data);
        setNestedReportData(nestedData);
      }
    } catch (e) {
      console.error(e);
    } finally {
      setLoading(false);
      dispatch(showLoader(false));
    }
  };
  const onSearch = values => {
    if (!Utils.isEmpty(values)) {
      const params = {
        locations: values.city,
        accounts: values.account,
      };
      setSearchParams(params);
      fetchBillingAgingReport(params);
    }
  };
  const handleOpenChange = newOpen => {
    setOpenPopOver(newOpen);
  };

  const fetchVisibleTableData = (obj, id) => {
    let _data = cloneDeep(currentVisibleTableData);
    let index = _data.findIndex(e => e.employeeId === id);
    _data[index].allocations = obj;
    currentVisibleTableData = _data;
    setExportVisibleData(_data);
  };

  const filterData = (obj, id, callBack) => {
    callBack(obj, id);
    setIsExported(false);
  };
  return (
    <>
      <Row align="middle" className="mb-3">
        <Col xs={24} sm={12} md={12} lg={24} xl={24}>
          <PageHeader
            className="p-2 pl-2"
            title={getMessage("sidenav.reports.billableAgingReport")}
            subTitle={
              <Popover
                placement="right"
                trigger="click"
                content={REPORTS_DESCRIPTION.billableAgingReport}
                open={openPopOver}
                onOpenChange={handleOpenChange}
              >
                <QuestionCircleTwoTone className="font-size-md mb-0" />
              </Popover>
            }
          />
        </Col>
      </Row>
      {BillableAgingReportData.length === 0 ? null : (
        <Row justify="space-between" gutter={[20, 20]} className="mt-4">
          <Col lg={24} md={24}>
            <div className="ant-affix mt-2 menu-bar">
              <Button type="primary" onClick={showDrawer} shape="circle" className="mr-3">
                <MenuFoldOutlined />
              </Button>
            </div>
          </Col>
        </Row>
      )}
      <Form
        form={form}
        onFinish={onSearch}
        layout="horizontal"
        wrapperCol={{ span: 16 }}
        labelCol={{ span: 8 }}
        labelAlign="left"
      >
        <Row gutter={[20, 5]} className="mb-3">
          <Col xl={8} lg={9} md={12} xs={24}>
            <Form.Item
              name={FILTER_FORM_FIELDS.CITY.name}
              label={getMessage(FILTER_FORM_FIELDS.CITY.label)}
              rules={[{ required: true }]}
            >
              <LocationPicker
                placeholder={getMessage(SELECT_TEXT.label, FILTER_FORM_FIELDS.CITY.label)}
                locations={allLocations}
                name={FILTER_FORM_FIELDS.CITY.name}
                form={form}
              />
            </Form.Item>
          </Col>

          <Col xl={8} lg={11} md={12} xs={24}>
            <Form.Item
              name={FILTER_FORM_FIELDS.ACCOUNT.name}
              label={getMessage(FILTER_FORM_FIELDS.ACCOUNT.label)}
              rules={[{ required: true }]}
            >
              <Select
                showSearch
                optionFilterProp={CHILDREN}
                mode="multiple"
                placeholder={getMessage(SELECT_TEXT.label, FILTER_FORM_FIELDS.ACCOUNT.label)}
                onSelect={value => Utils.onAccountSelection(value, form, FILTER_FORM_FIELDS.ACCOUNT.name)}
              >
                <Option value={ALL}>{ALL}</Option>
                {accountsList?.map((account, index) => (
                  <Option key={index} value={account.accountName}>
                    {account.accountName}
                  </Option>
                ))}
              </Select>
            </Form.Item>
          </Col>
          <Col xl={8} lg={24} md={24} xs={24}>
            <Button type="primary" icon={<SearchOutlined className="mr-2" />} htmlType="submit" className="float-right">
              {SEARCH.text}
            </Button>
          </Col>
        </Row>
      </Form>

      <Tabs type="card">
        <Tabs.TabPane tab="Report" key="report" className="pl-4 pr-3 pt-3" style={{ position: "relative" }}>
          {!!BillableAgingReportData.length && !!quickActionBtns.length && (
            <div className="is-mobile">
              <div className="floating-btn floating-buttons">
                {quickActionBtns.map(item => {
                  return (
                    <Tooltip title={item.description} placement="top">
                      <Button
                        type="primary"
                        className="floating-btn-circle my-1"
                        onClick={() => Utils.scrollIntoColumn(item.key)}
                      >
                        {item.title}
                      </Button>
                    </Tooltip>
                  );
                })}
              </div>
            </div>
          )}
          <TableComponent
            rowKey={(record, index) => `${record.employeeId}${index}`}
            columns={filteredColumns}
            data={BillableAgingReportData}
            scroll={{ x: true }}
            handleChange={handleChange}
            visibleData={data => {
              currentVisibleTableData = data;
            }}
            tableSize="small"
            visible={isMenuVisible}
            onDropdownMenuChange={handleMenuVisibleChange}
            showSorterTooltip={false}
            nestedTable={nestedTableRendered}
            tableKey={tableKey}
            defaultExpandAllRows={true}
          />
        </Tabs.TabPane>
        {/* <Tabs.TabPane tab="Summary" key="summary"           className="p-3"
>
          <BillableAgingReportSummary />
        </Tabs.TabPane>
        <Tabs.TabPane tab="Visual" key="visual">
          <BillableAgingReportVisual />
        </Tabs.TabPane> */}
      </Tabs>
      <Drawer onClose={onClose} visible={open} destroyOnClose width={250}>
        <TableMenuItem
          columns={generateDynamicColumns(dynamicColumns)}
          onColHide={onColHide}
          menuItems={MENU_ITEMS}
          onMenuItemClick={onMenuItemClick}
        />
      </Drawer>
    </>
  );
};
export default BillableAgingReport;
